/* Sidebar.module.css */
.sidebar {
    position: fixed;
    left: -250px; /* Начальное положение вне экрана */
    top: 0;
    height: 100%;
    width: 250px;
    background: #f6f5f5;
    transition: left 0.5s ease-in-out; /* Анимация открытия/закрытия */
    padding: 20px;
    box-shadow: 2px 0 5px rgba(0,0,0,0.5);
    z-index: 3;
}

.sidebar.open {
    left: 0; /* Положение когда открыто */
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 2; /* Поднимаем фон над всем остальным */
}

.nav {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.nav a {
    color: #100f0f;
    text-decoration: none;
    padding: 10px 15px;
    border-radius: 5px;
    transition: background 0.3s ease;
}

.nav a:hover {
    /*background: #f5efef;*/
    color: cornflowerblue;
}

.nav a.active {
    color: blue;
    /*background: #555;*/
    text-decoration: cornflowerblue;
}

.close {
    background: none;
    border: none;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 10px;
}

.close img {
    width: 20px;
    height: 20px;
}