.greetingForm {
    color: rgba(0, 0, 0, 0.5);
    font-family: Montserrat sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;

    text-align: left;
    margin: 40px 9px;
}

.inputAndButtonContainer {
    display: flex;
}

.error {
    color: rgb(204, 0, 0);
    font-family: Montserrat sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    letter-spacing: 0%;
    text-align: left;
}

.input {
    width: 372px;
    height: 36px;
    left: 0;
    top: 0;
}

.input:focus {
}

.errorInput {
    box-sizing: border-box;
    border: 1px solid rgb(204, 0, 0);
    border-radius: 5px;
}

.button {
    border-radius: 3px;

    background: rgb(0, 102, 204);
    opacity: 0.5;
    color: rgb(255, 255, 255);
    font-family: Montserrat sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    padding: 8px 24px;
}

